import { mapState } from "vuex";
export default {
    data() {
        return {
            title: "Hello",
            text: "text",
            opacity: 1
        };
    },
    computed: {
        ...mapState(["indexBlogs"])
    },
    onReady() {
        this.$store.dispatch("getIndexBlog");
    },
    async onPullDownRefresh() {
        this.lastPage = false;
        await this.$store.dispatch("getIndexBlog");
        uni.stopPullDownRefresh();
    },
    onNavigationBarSearchInputClicked() {
        uni.navigateTo({
            url: "../search/search",
            animationType: "fade-in"
        });
    },
    onReachBottom() {
        this.$store.dispatch("getIndexBlog");
    },
    methods: {
        async handleClickItem(e) {
            let { type, id } = e.target.dataset;
            if (type === undefined && id) {
                uni.navigateTo({
                    url: "../blog/blog?blogID=" + id
                });
            } else if (type) {
                switch (type) {
                    case "like":
                        let data = await this.$store.dispatch("toggleLike", id);
                    default:
                        break;
                }
                console.log(type);
            }
        },
        handleSearch() {
            uni.navigateTo({
                url: "../search/search"
            });
        },
        handleAdd() {
            console.log(2);
        }
    },
    onPageScroll(e) {
        let { scrollTop } = e;
        this.opacity = 200 / scrollTop;
    }
};
